import Vue from "vue";
import Vuex from "vuex";
// import { forestgreen } from "color-name";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    flights: [],
    filFlights:[],
    airports: [],
    depLocationName: null,
    depLocationCode: null,
    arrLocationName: null,
    arrLocationCode: null,
    depDate: null,
    returnDate: null,
    searchMessage: "No Search History",
    layoverAirports: [],
    layoverCount:0,
    numItemsPerPage:10,
    numPages:0,
    currentPage:1
  },
  mutations: {
    setSearchDetails: (state, payload) => {
      state.depLocationName = payload.depLoc;
      state.depLocationCode = payload.depLocCode;
      state.arrLocationName = payload.arrLoc;
      state.arrLocationCode = payload.arrLocCode;
      state.depDate = payload.depDate;
      state.returnDate = payload.retDate;
    },
    setFlighsResult: (state, payload) => {
      state.flights = payload;
      state.filFlights = payload.data;
      // creating flight filter items
      let layovers = [];
      let stops = [];
      state.flights.data.forEach(flight => {
        // layover
        if (flight.route.length > 1) {
          flight.route.forEach((stps, index) => {
            
            if (index == 0) {
              layovers.push({ name: stps.cityTo, code: stps.cityCodeTo });
            } else {
              layovers.push({ name: stps.cityFrom, code: stps.cityCodeFrom });
            }
          });
        }

        // stops
        stops.push(flight.route.length);
      });

      // removing duplicate entries
      let tempLyrKeys = new Set();
      let fLyrs = layovers.filter(
        item => !tempLyrKeys.has(item.code) && tempLyrKeys.add(item.code)
      );
      state.layoverAirports = fLyrs;

      // finding highest number of stops
      state.layoverCount = Math.max(...stops)

      // calculating page nums
      
    },
    setCurrntPage:(state,payload)=> {
      state.currentPage = payload;
    },
    filterFlightsList:(state,payload)=>{
      // console.log(payload)
      // console.log(payload.layover)
      // console.log(payload.stops)
      let layover = payload.layover;
      let stops = payload.stops;

      let lyrFlteredFlights = []

      if(layover.length >0){
        // console.log("###############################")
        // console.log("Filtering for layover")
        // console.log("###############################")
        for(let i=0;i<state.flights.data.length;i++){
          let flight = state.flights.data[i]
          // console.log(flight)
          for(let j=0;j<flight.route.length;j++){
            let rt = flight.route[j]
            for(let k=0;k<layover.length;k++){
              let ly = layover[k];
              // console.log(ly,rt)
              if(ly == rt.flyFrom){
                lyrFlteredFlights.push(flight);
                // console.log("Flight added",flight)
                break;
              }
            }
            
          }
        }

      }else{
        lyrFlteredFlights.push(...state.flights.data);
      }
      

      let stopsFilteredFlght = [];
      if(stops.length>0){
        // console.log("###############################")
        // console.log("Filtering for stops")
        // console.log("###############################")
        for(let i=0;i<lyrFlteredFlights.length;i++){
          let flight = lyrFlteredFlights[i];
          for (let j = 0; j < stops.length; j++) {
            let st = stops[j];
            if(flight.route.length == st){
              stopsFilteredFlght.push(flight);
              break;
            }            
          }
        }
        
      }else{
        stopsFilteredFlght = lyrFlteredFlights;
      }
      
      
      state.filFlights = stopsFilteredFlght;
    }
  },
  actions: {
    // filterFlightsList:{
      
    // }
  },
  modules: {},
  getters:{
    getPageCount:state =>{
      return Math.ceil(state.filFlights.length / state.numItemsPerPage)
    }
  }
});
