<template>
    <section class="section-filter border" v-if="flights != undefined && flights.length > 0">
        <h4>Filters</h4>
        <div class="row ">
            <div class="col-md-12 stops-card border-top">
                <h5>Layover Airports</h5>
                <ul class="layover-list">
                    <li v-for="(stop,index) in layovers" :key="index">
                        <input type="checkbox" :name="stop.name" :id="stop.code" @click="(e)=>handleLayoverFilter(e)">{{stop.name}}
                    </li>
                </ul>
            </div>            
        </div> 
        <div class="row">
            <div class="col-md-12 stops-card border-top">
                <h5>Stops</h5>
                <ul class="layover-list">
                    <li v-for="(stop,index) in stops" :key="index">
                        <input type="checkbox" :name="'stop_'+stop" :id="stop" @click="(e)=>handleStopsFilter(e)"> {{stop}}
                    </li>
                </ul>
            </div>            
        </div>   
        <div class="row">
            <div class="col-md-12 stops-card border-top">
                <button class="btn btn-danger btn-block rounded-pill" @click="filterItems">Apply</button>
            </div>            
        </div>        
    </section>
</template>
<script>
export default {
    name:"SearchFilters",
    data(){
        return {
            layoverFilterItems:[],
            stopsFilterItems:[]
        }
    },
    computed:{
        layovers(){
            return this.$store.state.layoverAirports;
        },
        flights(){
            return this.$store.state.flights.data;
        },
        stops(){
            return this.$store.state.layoverCount;
        }
    },
    methods:{
        handleLayoverFilter(e){
            // console.log(e.target.id)
            let isPresent = false;
            this.layoverFilterItems.forEach(item => {
                if(item == e.target.id){
                    isPresent = true;
                }
            })
            if(!isPresent){
                this.layoverFilterItems.push(e.target.id)
            }
            
        },
        handleStopsFilter(e){
            // console.log(e.target.id)
            let isPresent = false;
            this.stopsFilterItems.forEach(item => {
                if(item == e.target.id){
                    isPresent = true;
                }
            })
            if(!isPresent){
                this.stopsFilterItems.push(e.target.id)
            }
            
        },
        filterItems(){
            // console.log("filter method called")
            // console.log(this.layoverFilterItems.length,this.stopsFilterItems.length)
            if(this.layoverFilterItems.length > 0 || this.stopsFilterItems.length>0 ){
                // console.log("filtering")
                this.$store.commit("filterFlightsList",{
                    layover:this.layoverFilterItems,
                    stops:this.stopsFilterItems
                })
            }
        }
    }
}
</script>
<style>
.section-filter{
    margin-left:20px;
    padding:20px;
    margin-top:20px;
}
.layover-list{
    list-style-type: none;
    max-height:200px;
    overflow:hidden; 
    overflow-y:scroll;
    
}
.stops-card{
    text-align:left;
    margin-top:20px;
    padding-top:20px;
}
</style>