<template>
  <div class="search-message">
    <a
      class="btn btn-danger page-lnk"
      @click="e => changePageNum(e)"
      v-for="(page, index) in getPageCount"
      :id="page"
      :key="index"
      ><span class="page-num">{{ page }}</span></a
    >
  </div>
</template>
<script>
import {mapGetters} from "vuex"
export default {
  name: "Pagination",
  computed: {   
    ...mapGetters(['getPageCount'])
  },
  methods: {
    changePageNum(event) {
      // console.log(event)
      this.$store.commit("setCurrntPage", event.target.id);
    }
  }
};
</script>
<style scoped>
.page-num{
    color:white;
}
.page-lnk{
    margin:2px;
}
</style>
